<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div class="p-4 card xl:p-5">
    <form
      @input="request.errors.clear($event.target.name)"
      @submit.prevent="submit()"
    >
      <div class="space-y-4">
        <div>
          <h3 class="text-xl font-semibold">
            {{
              $t("labels.group_patients_title", {
                workspace: group.workspace_name,
                group: group.name,
              })
            }}
          </h3>
        </div>

        <div>
          <label for="textSize">{{ $t("attributes.size") }}</label>
          <span class="relative flex mt-1.5">
            <input
              v-model="group.size"
              id="textSize"
              name="size"
              type="number"
              max="10000"
              class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              @change="group.fillPatients()"
            />
            <span
              class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
            >
              <v-icon name="chevron-up-down" size="6"></v-icon>
            </span>
          </span>

          <validation-feedback>
            {{ request.errors.get("size") }}
          </validation-feedback>
        </div>

        <div class="sticky flex gap-x-4 bg-white py-4 top-[61px] z-1">
          <div class="w-10">#</div>
          <div class="flex-1 font-medium">
            <span>{{ $t("attributes.patient") }}</span>
          </div>
          <div class="flex-1 font-medium">
            {{ $t("attributes.phone") }}
          </div>
          <div class="flex-1 font-medium">
            {{ $t("attributes.contact_phone") }}
          </div>
          <div class="w-10"></div>
        </div>

        <div
          v-for="(patient, index) in group.patients"
          :key="index"
          class="flex gap-x-4"
        >
          <div class="w-10 self-center font-medium">{{ index + 1 }}.</div>
          <div class="flex-1">
            <input
              v-model="patient.name"
              :name="`patient${index}Name`"
              :id="`textPatient${index}Name`"
              type="text"
              class="h-9 w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              autocomplete="off"
            />
          </div>
          <div class="flex-1">
            <v-mask
              v-model="patient.phone"
              :options="{ phone: true, phoneRegionCode: 'tr' }"
              :name="`patient${index}Phone`"
              :id="`textPatient${index}Phone`"
              type="text"
              class="h-9 w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              autocomplete="off"
            ></v-mask>
          </div>
          <div class="flex-1">
            <v-mask
              v-model="patient.contact_phone"
              :options="{ phone: true, phoneRegionCode: 'tr' }"
              :name="`patient${index}ContactPhone`"
              :id="`textPatient${index}ContactPhone`"
              type="text"
              class="h-9 w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              autocomplete="off"
            ></v-mask>
          </div>
          <div class="w-10">
            <v-button
              color="transparent"
              icon
              :title="$t('labels.delete')"
              @click="group.removePatient(index)"
            >
              <i class="fa fa-lg fa-times"></i>
            </v-button>
          </div>
        </div>

        <div>
          <v-button
            color="transparent"
            block
            weight="normal"
            align="end"
            @click="group.addPatient()"
          >
            <span class="mr-2">{{ $t("labels.add_patient") }}</span>
            <i class="fa fa-lg fa-plus"></i>
          </v-button>
        </div>

        <div class="text-end space-x-3">
          <v-button
            type="submit"
            color="primary"
            :disabled="!request.submittable"
          >
            {{ $t("labels.submit") }}
          </v-button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Group from "@/models/Group";
import ModelRequest from "@/request/ModelRequest";

export default {
  name: "GroupPatients",
  props: {
    id: {
      type: [Number, String],
      required: false,
    },
  },
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.workspaces"), route: "app.workspaces" },
        { title: this.$t("titles.group") },
        {
          title: this.$t("titles.patients"),
          active: true,
        },
      ],
      group: new Group(),
      request: new ModelRequest(),
    };
  },
  created() {
    this.group.fetch(this.id).then(() => {
      this.group.fillPatients();
    });

    this.request.bind(this.group);
  },
  methods: {
    submit() {
      this.request.update().then(() => {
        this.group.fillPatients();
      });
    },
  },
};
</script>